@import "./components/header";
@import "zeroing";
@import "important";
@import "font-face";
@import "mixins";

.container {
    width: 1170px;
    margin: 0 auto;
}
.header .burger__menu._active .burger__container .burger__navigation .link__two {
    color: #FEAB47;
}
.header .header__section .header__navigation .link__two {
    color: #FEAB47;
}
.block__game-phone {
    display: none;
}

.section:nth-child(1) {
    padding-top: 17px;
    .section__block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .block__text {
            h1 {
                width: 596px;
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 600;
                font-size: 56px;
                line-height: 135.4%;
                color: #000000;
            }
            p {
                padding-top: 50px;
                font-family: 'Nunito';
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 600;
                font-size: 15px;
                line-height: 20px;
                color: #000000;
                width: 455px;
            }
        }
    }
}
.section:nth-child(2) {
    padding-bottom: 120px;
    .block__game {
        display: flex;
        flex-direction: column;
        .container__block {
            display: flex;
            align-items: s;
            padding-bottom: 30px;
            width: 100%;
            .card:nth-child(2) {
                margin: 0px 30px;
            }
        }
        .card {
            cursor: pointer;
            width: 370px;
            height: 380px;
            background: #FFFFFF;
            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
            border-radius: 10px 10px 0px 0px;

            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
            transition: all 0.5s ease-out;
            .card__game {
                padding: 20px;
                .block__line {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding-top: 30px;
                    h3 {
                        font-family: 'Nunito';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 27px;
                        line-height: 37px;
                        color: #000000;
                    }
                    .line {
                        border: 2px solid #FEA246;
                        width: 35px;
                        border-radius: 5px;
                    }
                }
                p {
                    padding-top: 30px;
                    width: 100%;
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 22px;
                    color: #000000;
                }
            }
            .block__text {
                display: none;
            }
        }
        .card:hover {
            box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.25);
            transition: box-shadow 0.5s ease-out;
            transition: height 0.3s ease;
        }
        .card.active {
            height: 512px;
            cursor: pointer;
            width: 370px;
            background: #FFFFFF;
            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
            border-radius: 10px 10px 0px 0px;
            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
            transition: all 0.5s ease-out;
            .card__game {
                padding: 20px;
                background-color: white;
                .block__line {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding-top: 30px;
                    h3 {
                        font-family: 'Nunito';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 27px;
                        line-height: 37px;
                        color: #000000;
                    }
                    .line {
                        border: 2px solid #515151;
                        width: 35px;
                        border-radius: 5px;
                    }
                }
                p {
                    padding-top: 30px;
                    width: 100%;
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 22px;
                    color: #000000;
                }
            }
            .block__text {
                display: flex;
                flex-direction: column;
                align-items: start;
                display: block;
                padding-top: 30px;
                span {
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 300;
                    font-size: 12px;
                    line-height: 16px;
                    color: #000000;
                }
                hr {
                    margin-top: 20px;
                    border: 1px solid #515151;
                    width: 330px;
                }
                .icon {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding-top: 20px;
                    width: 148px;
                    img {
                        width: 40px;
                        height: 40px;
                        border: 1px solid #c4c4c4;
                        border-radius: 20px
                    }
                }
            }
        }
    }
}

@import "./components/footer";
@import "breakpoints/breakpoint-game";

.footer .footer__container .footer__section .footer__navigation .link__two {
    color: #FEAB47;
}
.footer .footer__container-phone .footer__section .footer__section-left .footer__navigation .link__two {
    color: #FEAB47;
}