@media screen and(max-width: 576px) {
    .container {
        width: 85%;
    }
    .section:nth-child(1) .section__block .block__text {
        width: 100%;
    }
    .section:nth-child(1) .section__block .block__text h1 {
        width: 100%;
        font-size: calc(25px + (16 + 16 * 0.7) * ((100vw - 320px) / 576));
        line-height: 135.4%;
    }
    .section:nth-child(1) .section__block .block__text p {
        width: 75%;
        padding-top: 20px;
        font-size: 13px;
    }
    .section:nth-child(1) .section__block {
        flex-direction: column;
    }
    .section:nth-child(2) .block__game {
        display: none;
    }
    .section:nth-child(2) .block__game-phone {
        display: block;
        .container__block {
            display: grid;
            grid-template-rows: repeat(2, 1fr);
            grid-template-columns: repeat(2, 1fr);
            gap: 10px;
            .card {
                width: 100%;
                height: 230px;
                background: #FFFFFF;
                box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.25);
                border-radius: 10px 10px 0px 0px;
                .card__game {
                    padding: 5px;
                    img {
                        width: 100%;
                    }
                    .block__line {
                        padding-top: 5px;
                        h3 {
                            font-family: 'Nunito';
                            font-style: normal;
                            font-weight: 400;
                            font-size: 17px;
                            line-height: 22px; 
                            color: #000000;
                        }
                        .line {
                            margin-top: 10px;
                            border-radius: 5px;
                            width: 25px;
                            border: 1px solid #FEA246;
                        }
                    }
                    p {
                        margin-top: 15px;
                        font-size: 12px;
                        line-height: 122.9%;
                        color: #000000;
                    }
                    .block__text {
                        display: none;
                    }
                    .block__img {
                        padding-top: 20px;
                        hr {
                            width: 100%;
                            border: 0.6px solid #515151;
                            border-radius: 4px;
                            margin-bottom: 20px;
                        }
                        a {
                            padding-right: 5px;
                            img {
                                width: 20px;
                                height: 20px;
                                border-radius: 5px;
                                filter: drop-shadow(0px 1px 3px rgba(0, 0, 0, 0.25));                            }   
                        }
                    }
                }  
            }
            .card.active {
                height: 290px;
                .block__text {
                    display: block;
                    padding-top: 20px;
                        hr {
                            width: 100%;
                            border: 0.6px solid #515151;
                            border-radius: 4px;
                            margin-bottom: 20px;
                        }
                        a {
                            padding-right: 5px;
                            img {
                                width: 20px;
                                height: 20px;
                                filter: drop-shadow(0px 1px 3px rgba(0, 0, 0, 0.25));                            }   
                        }
                }
            }
        }
    }
}